nav {
    position: -webkit-sticky;
    position: sticky;
    top:0;
    background-color:#ffe26f;
    box-shadow: 0px 0px 10px #B7B7B7;
    height: 60px;
    width: 100%;
  }
  
  label.logo {
    color:black;
    font-size: 30px;
    line-height: 60px;
    padding: 0 50px;
    font-weight: bold;
  }
  nav ul {
    float: right;
    margin-right: 20px;
  }
  nav ul li {
    display: inline-block;
    line-height: 60px;
    margin: 0 5px;
  }
  nav ul li a {
    color:black;
    font-size: 16px;
    font-weight: bold;
    padding: 7px 13px;
    border-radius: 3px;
    text-decoration: none;
    text-transform: uppercase;
  }
  
  .active{
    background: #012443;
    border-radius: 20px;
    color: white;
  }
  a:hover {
    background: #012443;
    -webkit-transform: 0.5s;
            transform: 0.5s;
    border-radius: 20px;
    color:white
  }
  .checkbtn {
    font-size: 30px;
    color: #012443;
    float: right;
    line-height: 60px;
    margin-right: 40px;
    margin-top: 13px;
    cursor: pointer;
    display: none;
  }
  #check {
    display: none;
  }
  
  
  
  @media  (max-width: 1024px){
    label.logo {
      font-size: 15px;
      font-weight: bold;
      padding-left: 20px;
    }
    nav ul li a {
      font-size: 16px;
    }
  }
  @media (max-width: 1024px){
    .checkbtn {
      display: flex;
    }
    nav ul {
      position: fixed;
      width: 100%;
      height: 100vh;
      background: #012443;
      top: 60px;
      left: -100%;
      text-align: center;
      transition: all 0.5s;
    }
    nav ul li {
      display: flex;
      justify-content: center;
      
    }
    nav ul li a {
      font-size: 20px;
      color:white
    }
  
   
    nav ul li a:hover {
      
     border-radius: 20px;
     border: 2px solid;
    border-radius: 20px;
      color:white;
    -webkit-transform: 0.5s;
            transform: 0.5s;
    }
    #check:checked ~ ul {
      left: 0;
    }
  }
  
